import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    [theme.breakpoints.up('lg')]: {
      justifyContent: 'flex-end',
    },
  },
  weatherWrapper: {
    background: theme.palette.secondary.main,
    boxShadow: '0px 2px 20px 0px rgba(0, 0, 0, 0.08)',
    padding: `${pxToRem(24)} ${pxToRem(24)} ${pxToRem(16)}`,
    position: 'relative',
    top: pxToRem(16),
    margin: `${pxToRem(-150)} ${pxToRem(24)} 0`,
    display: 'flex',
    flexDirection: 'column',
    ...theme.mixins.right(0),
    borderBottom: theme.borders.tab,
    borderColor: params => params.stripColor || theme.palette.common.Sunrise,
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.marginLR('auto', pxToRem(93)),
      marginTop: pxToRem(-284),
      padding: `${pxToRem(40)} ${pxToRem(40)} ${pxToRem(24)}`,
    },
    '& .weatherPart': {
      display: 'flex',
      justifyContent: 'space-between',
      flexDirection: 'column',
      [theme.breakpoints.up('lg')]: {
        flexDirection: 'row',
      },

      '& .leftPart': {
        display: 'flex',
        flexDirection: 'column',
        marginTop: pxToRem(32),
        [theme.breakpoints.up('lg')]: {
          marginTop: pxToRem(70),
        },

        '& svg': {
          height: pxToRem(32),
          width: pxToRem(32),
          [theme.breakpoints.up('lg')]: {
            height: pxToRem(64),
            width: pxToRem(64),
          },
          '& path': {
            fill: theme.palette.primary.main,
          },
        },
      },

      '& .rightPart': {
        display: 'flex',
        marginTop: pxToRem(18),
        [theme.breakpoints.up('lg')]: {
          marginTop: pxToRem(16),
        },
      },

      '& .tempActions': {
        display: 'flex',
        alignItems: 'flex-end',
        position: 'relative',
        bottom: pxToRem(16),
        ...theme.mixins.left(pxToRem(-15)),
        right: theme.isDirectionRTL && 0,
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.left(pxToRem(0)),
          bottom: pxToRem(32),
        },
        '& .temp': {
          order: 1,
          padding: `0 ${pxToRem(16)}`,
          cursor: 'pointer',
          lineHeight: pxToRem(20),
          color: theme.mixins.Black(500),
          '&:last-child': {
            borderRight: 0,
          },
          '&.active': {
            order: 0,
            ...theme.mixins.borderRight(theme.borders.divider),
            color: theme.palette.primary.main,
          },
        },
      },
    },
  },
  degree: {
    fontSize: pxToRem(32),
    position: 'relative',
    top: pxToRem(-6),
    [theme.breakpoints.up('lg')]: {
      fontSize: pxToRem(72),
      top: pxToRem(-8),
    },
  },
  temp: {
    fontSize: pxToRem(80),
    lineHeight: pxToRem(96),
    position: 'relative',
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.right(pxToRem(-35)),
      left: theme.isDirectionRTL && 0,
      fontSize: pxToRem(160),
      lineHeight: pxToRem(190),
    },
  },
  date: {
    fontSize: pxToRem(20),
    lineHeight: pxToRem(24),
    [theme.breakpoints.up('lg')]: {
      fontSize: pxToRem(32),
      lineHeight: pxToRem(32),
    },
    '&.weatherCondition': {
      [theme.breakpoints.down('md')]: {
        fontSize: pxToRem(24),
        lineHeight: pxToRem(29),
      },
    },
  },
}));

export default useStyles;
