import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import clsx from 'clsx';
import get from 'lodash/get';
import { createStructuredSelector } from 'reselect';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';

import ICON_MAPPING from '../../../components/organism/LiveWeatherInfo/iconMapping';
import { setTemperature as setTemperatureAction } from '../../../components/organism/LiveWeatherInfo/redux/actions';
import { getSelectedTemperature } from '../../../components/organism/LiveWeatherInfo/redux/selectors';
import { getLiveWeatherInfoStart } from '../../../components/organism/LiveWeatherInfo/redux/actions';
import { selectLiveWeatherData } from '../../../components/organism/LiveWeatherInfo/redux/selectors';
import { getFormattedDate } from '../../../utils/momentUtil';
import Grid from '../../../components/molecules/Grid';
import Box from '../../../components/molecules/Box';
import DctSvgIcon from '../../../components/atoms/Svg';
import Typography from '../../../components/atoms/Typography';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import useStyles from './style';
import { availableTemperatures, defaultTemperature } from '../../../../constants';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../utils/analytics';
import {
  getPageName,
  getComponentName,
} from '../../../utils/getPageComponentInfo';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';

const CurrentWeather = props => {
  const {
    liveWeatherData,
    className,
    setTemperature,
    selectedTemperature,
    params,
    getLiveWeatherInfoStart,
  } = props;

  const {
    imageName,
    weatherCondition,
    temperatureC,
    temperatureF,
  } = get(liveWeatherData, 'data.results.data', {}) || {};
  const classes = useStyles(params);
  const language = get(props, 'sitecoreContext.language', '');

  useEffect(() => {
    if (language) {
      getLiveWeatherInfoStart(language);
    }
  }, [language]);

  const addAnalyticsInfo = val => {
    let levelThreeText = '';
    if (val === defaultTemperature) {
      levelThreeText = `${get(analyticsData, 'dataLinksText.temperatureFahrenheit', '')} | ${get(analyticsData, 'dataLinksText.temperatureCelsius', '')}`;
    } else {
      levelThreeText = `${get(analyticsData, 'dataLinksText.temperatureCelsius', '')} | ${get(analyticsData, 'dataLinksText.temperatureFahrenheit', '')}`;
    }
    const obj = {
      ...analyticsData.pageBodyInteraction.temperatureToggle,
      ...{
        label: createDynamicLabels([getPageName(props), getComponentName(props), levelThreeText]),
      },
    };
    pushAnaylyticsData(obj);
  };

  return temperatureC ? (
    <Grid
      container
      className={classes.root}
    >
      <Grid
        item
        xs={12}
        md={12}
        lg={5}
      >
        <div className={`${classes.weatherWrapper} ${className}`}>
          <Typography
            component="h5"
            variant="body2"
            className={classes.date}
          >
            {getFormattedDate('dddd, MMMM D, YYYY')}
          </Typography>
          <div className="weatherPart">
            <div className="leftPart">
              {ICON_MAPPING[imageName] && (
                <Box component="span">
                  <DctSvgIcon name={ICON_MAPPING[imageName]} />
                </Box>
              )}
              <Typography
                component="h5"
                variant="body2Bold"
                className={clsx(classes.date, 'weatherCondition')}
              >
                {weatherCondition}
              </Typography>
            </div>
            <div className="rightPart">
              <Typography
                component="h2"
                variant="body1"
                className={classes.temp}
              >
                {selectedTemperature === defaultTemperature ? temperatureC : temperatureF}
                <Typography
                  component="sup"
                  variant="body2"
                  className={classes.degree}
                >
                  &deg;
              </Typography>
              </Typography>
              <Typography
                component="span"
                variant="body2"
                className="tempActions"
              >
                {availableTemperatures.map(val => (
                  <Typography
                    key={val}
                    component="span"
                    variant="body1Bold"
                    onClick={() => {
                      addAnalyticsInfo(val);
                      setTemperature(val);
                    }}
                    className={clsx('temp', {
                      active: val === selectedTemperature,
                    })}
                  >
                    {val}
                  </Typography>
                ))}
              </Typography>
            </div>
          </div>
        </div>
      </Grid>
    </Grid>
  ) : null;
};

const mapDispatchToProps = dispatch => {
  return {
    setTemperature: value => dispatch(setTemperatureAction(value)),
    getLiveWeatherInfoStart: language => dispatch(getLiveWeatherInfoStart(language)),
  };
};

const mapStateToProps = createStructuredSelector({
  selectedTemperature: getSelectedTemperature,
  liveWeatherData: selectLiveWeatherData,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorBoundary(withSitecoreContext()(CurrentWeather)));

export { CurrentWeather as CurrentWeatherInfo };
